import React from 'react'
import Link from 'gatsby-link'
import get from 'lodash/get'
import Helmet from 'react-helmet'
import SectionTitle from '../components/SectionTitle'
import { rhythm } from '../utils/typography'

class AboutIndex extends React.Component {
  render() {
    return (
      <div >
        <Helmet title={get(this, 'props.data.site.siteMetadata.title')} />
        <SectionTitle> About </SectionTitle>

        <div className="bordered-left"> 
          <p> Hi! I am Mark. </p>
          <p> 
            I am a software engineer with 7+ years of experience developing 
            fast, efficient, secure and user friendly products.
          </p>
          <p>
            This blog was made for sole purpose of documenting my learnings and side projects.
          </p>
          <p>
            For development, I use Python and PHP for backend and ReactJS for frontend.
          </p>
          <p>
            If you want to get in touch, you can email <i>markronquillo23@gmail.com</i> or call <i>+639175006812</i>.
          </p>
        </div>
      </div>
    )
  }
}

AboutIndex.propTypes = {
  route: React.PropTypes.object,
}

export default AboutIndex



